"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Progress } from '@/components/ui/progress'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'

interface JobPreferences {
  position: string
  location: string
  salaryMin: string
  experience: string
  keywords: string
}

export default function UploadCV() {
  const [file, setFile] = useState<File | null>(null)
  const [isUploading, setIsUploading] = useState(false)
  const [progress, setProgress] = useState(0)
  const [results, setResults] = useState<any[]>([])
  const [showResults, setShowResults] = useState(false)
  const [preferences, setPreferences] = useState<JobPreferences>({
    position: '',
    location: '',
    salaryMin: '',
    experience: '',
    keywords: ''
  })

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      const selectedFile = e.target.files[0]
      
      // Validar tipo de archivo
      const allowedTypes = ['application/pdf', 'application/msword', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document']
      if (!allowedTypes.includes(selectedFile.type)) {
        alert('Por favor selecciona un archivo PDF o Word (.doc, .docx)')
        return
      }

      // Validar tamaño (máximo 5MB)
      if (selectedFile.size > 5 * 1024 * 1024) {
        alert('El archivo es demasiado grande. Máximo 5MB.')
        return
      }

      setFile(selectedFile)
    }
  }

  const handlePreferenceChange = (field: keyof JobPreferences, value: string) => {
    setPreferences(prev => ({
      ...prev,
      [field]: value
    }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!file) {
      alert('Por favor selecciona un archivo CV')
      return
    }

    if (!preferences.position || !preferences.location) {
      alert('Por favor completa al menos el puesto y la ubicación deseada')
      return
    }

    setIsUploading(true)
    setProgress(0)
    setShowResults(false)

    try {
      const formData = new FormData()
      formData.append('cvFile', file)
      formData.append('preferences', JSON.stringify(preferences))

      // Simular progreso
      const progressInterval = setInterval(() => {
        setProgress(prev => {
          if (prev >= 90) {
            clearInterval(progressInterval)
            return prev
          }
          return prev + Math.random() * 10
        })
      }, 500)

      const response = await fetch('/api/apply', {
        method: 'POST',
        body: formData,
      })

      clearInterval(progressInterval)
      setProgress(100)

      if (!response.ok) {
        throw new Error('Error en el envío')
      }

      const data = await response.json()
      setResults(data.results || [])
      setShowResults(true)

      // Guardar estadísticas
      const stats = {
        total: data.results.length,
        successful: data.results.filter((r: any) => r.status === 'success').length,
        failed: data.results.filter((r: any) => r.status === 'failed').length,
        pending: 0
      }

      localStorage.setItem('applicationStats', JSON.stringify(stats))
      localStorage.setItem('recentApplications', JSON.stringify(
        data.results.map((r: any) => ({
          ...r,
          timestamp: new Date().toLocaleString('es-ES')
        }))
      ))

    } catch (error) {
      console.error('Error:', error)
      alert('Hubo un error al enviar las aplicaciones. Por favor intenta de nuevo.')
    } finally {
      setIsUploading(false)
    }
  }

  const resetForm = () => {
    setFile(null)
    setProgress(0)
    setResults([])
    setShowResults(false)
    setPreferences({
      position: '',
      location: '',
      salaryMin: '',
      experience: '',
      keywords: ''
    })
  }

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      {/* Header */}
      <div className="text-center">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">
          Subir CV y Enviar a Trabajos
        </h1>
        <p className="text-xl text-gray-600">
          Carga tu CV y configura tus preferencias para enviar automáticamente a 100 ofertas de trabajo
        </p>
      </div>

      {!showResults ? (
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* File Upload */}
          <Card>
            <CardHeader>
              <CardTitle>1. Subir tu CV</CardTitle>
              <CardDescription>
                Selecciona tu CV en formato PDF o Word (máximo 5MB)
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <div className="flex items-center justify-center w-full">
                  <label className="flex flex-col items-center justify-center w-full h-32 border-2 border-gray-300 border-dashed rounded-lg cursor-pointer bg-gray-50 hover:bg-gray-100">
                    <div className="flex flex-col items-center justify-center pt-5 pb-6">
                      <p className="mb-2 text-sm text-gray-500">
                        <span className="font-semibold">Haz clic para subir</span> o arrastra y suelta
                      </p>
                      <p className="text-xs text-gray-500">PDF, DOC o DOCX (MAX. 5MB)</p>
                    </div>
                    <Input
                      type="file"
                      className="hidden"
                      accept=".pdf,.doc,.docx"
                      onChange={handleFileChange}
                    />
                  </label>
                </div>
                {file && (
                  <div className="p-3 bg-green-50 border border-green-200 rounded-lg">
                    <p className="text-sm text-green-800">
                      ✓ Archivo seleccionado: {file.name} ({(file.size / 1024 / 1024).toFixed(2)} MB)
                    </p>
                  </div>
                )}
              </div>
            </CardContent>
          </Card>

          {/* Job Preferences */}
          <Card>
            <CardHeader>
              <CardTitle>2. Configurar Preferencias de Trabajo</CardTitle>
              <CardDescription>
                Personaliza tu búsqueda para obtener mejores resultados
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="position">Puesto Deseado *</Label>
                  <Input
                    id="position"
                    placeholder="ej: Desarrollador Frontend, Marketing Manager"
                    value={preferences.position}
                    onChange={(e) => handlePreferenceChange('position', e.target.value)}
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="location">Ubicación *</Label>
                  <Input
                    id="location"
                    placeholder="ej: Madrid, Barcelona, Remoto"
                    value={preferences.location}
                    onChange={(e) => handlePreferenceChange('location', e.target.value)}
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="salary">Salario Mínimo (€/año)</Label>
                  <Input
                    id="salary"
                    type="number"
                    placeholder="ej: 30000"
                    value={preferences.salaryMin}
                    onChange={(e) => handlePreferenceChange('salaryMin', e.target.value)}
                  />
                </div>
                <div>
                  <Label htmlFor="experience">Años de Experiencia</Label>
                  <Input
                    id="experience"
                    placeholder="ej: 2-5 años"
                    value={preferences.experience}
                    onChange={(e) => handlePreferenceChange('experience', e.target.value)}
                  />
                </div>
              </div>
              <div>
                <Label htmlFor="keywords">Palabras Clave</Label>
                <Textarea
                  id="keywords"
                  placeholder="ej: React, JavaScript, Node.js, MongoDB"
                  value={preferences.keywords}
                  onChange={(e) => handlePreferenceChange('keywords', e.target.value)}
                  rows={3}
                />
              </div>
            </CardContent>
          </Card>

          {/* Submit Button */}
          <Card>
            <CardContent className="pt-6">
              <Button 
                type="submit" 
                className="w-full text-lg py-6" 
                disabled={isUploading || !file}
              >
                {isUploading ? 'Enviando a 100 Trabajos...' : 'Enviar CV a 100 Trabajos'}
              </Button>
            </CardContent>
          </Card>
        </form>
      ) : null}

      {/* Progress */}
      {isUploading && (
        <Card>
          <CardHeader>
            <CardTitle>Enviando Aplicaciones...</CardTitle>
            <CardDescription>
              Procesando tu CV y enviando a múltiples portales de trabajo
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <Progress value={progress} className="h-3" />
              <p className="text-center text-sm text-gray-600">
                {progress < 100 ? `Progreso: ${Math.round(progress)}%` : 'Finalizando...'}
              </p>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Results */}
      {showResults && (
        <div className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle>Resultados del Envío</CardTitle>
              <CardDescription>
                Resumen de las aplicaciones enviadas a diferentes portales de trabajo
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
                <div className="text-center p-4 bg-green-50 rounded-lg">
                  <div className="text-2xl font-bold text-green-600">
                    {results.filter(r => r.status === 'success').length}
                  </div>
                  <div className="text-sm text-green-800">Exitosas</div>
                </div>
                <div className="text-center p-4 bg-red-50 rounded-lg">
                  <div className="text-2xl font-bold text-red-600">
                    {results.filter(r => r.status === 'failed').length}
                  </div>
                  <div className="text-sm text-red-800">Fallidas</div>
                </div>
                <div className="text-center p-4 bg-blue-50 rounded-lg">
                  <div className="text-2xl font-bold text-blue-600">
                    {results.length}
                  </div>
                  <div className="text-sm text-blue-800">Total</div>
                </div>
              </div>

              <div className="space-y-2 max-h-96 overflow-y-auto">
                {results.map((result, index) => (
                  <div key={index} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <span className="font-medium">{result.jobBoard}</span>
                    <div className="flex items-center space-x-2">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                        result.status === 'success' 
                          ? 'bg-green-100 text-green-800' 
                          : 'bg-red-100 text-red-800'
                      }`}>
                        {result.status === 'success' ? 'Exitosa' : 'Fallida'}
                      </span>
                    </div>
                  </div>
                ))}
              </div>

              <div className="flex space-x-4 mt-6">
                <Button onClick={() => window.location.href = '/'} className="flex-1">
                  Ver Dashboard
                </Button>
                <Button variant="outline" onClick={resetForm} className="flex-1">
                  Enviar Otro CV
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  )
}
